/**
 * 模拟AI回答生成
 * @param {string} question - 用户问题
 * @param {Object} contextInfo - 上下文信息
 * @returns {string} AI回答
 */
export const generateMockAIResponse = (question, contextInfo) => {
  // 简单的规则匹配来生成回答
  if (question.includes('你好') || question.includes('嗨') || question.includes('Hi')) {
    return '你好！很高兴为你服务。请问有什么问题需要帮助？';
  }
  
  if (question.includes('学校') || question.includes('大学') || contextInfo.mode === 'school') {
    return '选择合适的学校需要考虑多方面因素，包括学校排名、专业实力、地理位置、学费和奖学金情况等。你可以结合自己的学术兴趣、职业规划和个人偏好来做决定。你有特别感兴趣的专业或学校吗？';
  }
  
  if (question.includes('职业') || question.includes('工作') || contextInfo.mode === 'career') {
    return '职业规划是一个长期过程，需要结合个人兴趣、能力、价值观和市场需求来考虑。建议你先了解自己的优势和热情所在，然后探索相关的职业领域，制定切实可行的短期和长期目标。你有特别感兴趣的职业领域吗？';
  }
  
  if (question.includes('学习') || question.includes('效率') || contextInfo.mode === 'learning') {
    return '提高学习效率的关键在于良好的时间管理、专注力培养和有效的学习方法。建议你尝试番茄工作法、主动学习策略，并保持规律的作息和健康的生活方式。不同的学科可能需要不同的学习方法，你是在学习哪方面的内容呢？';
  }
  
  // 默认回答
  return `感谢你的提问。我会尽力提供有价值的信息和建议。关于"${question}"，你能否提供更多具体的信息或背景，这样我可以给你更有针对性的回答。`;
};

/**
 * 模拟API返回结果
 * @param {boolean} success - 是否成功
 * @param {*} data - 返回数据
 * @param {Object} [error] - 错误信息（如果有）
 * @returns {Object} 格式化的API返回结果
 */
export const mockApiResponse = (success, data, error = null) => {
  if (success) {
    return {
      success: true,
      data
    };
  } else {
    return {
      success: false,
      error: error || {
        message: '请求失败'
      }
    };
  }
}; 